import React from 'react';
import './App.css';
import Container from '@material-ui/core/Container';
import Button from '@material-ui/core/Button';
import Paper from '@material-ui/core/Paper';

import Grid from '@material-ui/core/Grid';




class Application extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      currentNumber: "0",
      operatorFlag: false,
      decimalFlag: false
    }
  }

  handleClick = (buttonName) => {
    let currentNumber = this.state.currentNumber
    let operatorFlag = this.state.operatorFlag
    switch (true) {

      // Xử lí thêm bớt chuỗi số
      case buttonName === "0" ||
        buttonName === "1" ||
        buttonName === "2" ||
        buttonName === "3" ||
        buttonName === "4" ||
        buttonName === "5" ||
        buttonName === "6" ||
        buttonName === "7" ||
        buttonName === "8" ||
        buttonName === "9":
        if (this.state.currentNumber !== "0") {
          currentNumber += buttonName
          operatorFlag = false
        } else {
          currentNumber = buttonName
        }
        break

      //xử lí dấu, 2 dấu liên tiếp thì lấy dấu cuối, case đặc biệt (-) thì vẫn dữ nguyên
      case buttonName === "+" ||
        buttonName === "-" ||
        buttonName === "*" ||
        buttonName === "/":
        if (buttonName === "-" && currentNumber[currentNumber.length - 1] !== "-") {
          currentNumber += buttonName
          operatorFlag = true
          this.setState({ decimalFlag: false })
        }

        else {
          if (!this.state.operatorFlag) {
            currentNumber += buttonName
            operatorFlag = true
            this.setState({ decimalFlag: false })
          } else {
            if (currentNumber[currentNumber.length - 2] === "+" || currentNumber[currentNumber.length - 2] === "*" || currentNumber[currentNumber.length - 2] === "/") {
              currentNumber = currentNumber.slice(0, currentNumber.length - 2)
              currentNumber += buttonName
            }
            else {
              currentNumber = currentNumber.slice(0, currentNumber.length - 1)
              currentNumber += buttonName
            }
          }
        }
        
        break
        
        //ALLclear Button
        case buttonName === "AC":
          currentNumber = "0"
          operatorFlag = false
          this.setState({ decimalFlag: false })
          break
          
          //Equal
          case buttonName === "=":
            currentNumber = eval(currentNumber)
            operatorFlag = false
            this.setState({ decimalFlag: true })
            break
            
            //Toán tử Decimal
            case buttonName === ".":
              if (!this.state.decimalFlag) {
                currentNumber += "."
                this.setState({ decimalFlag: true })
              }
    }
    this.setState({ operatorFlag })
    this.setState({ currentNumber })
  }
  
  render() {
    return (
      <div>

        <div> <code style={{color:"rgb(255, 51, 119)"}}><h1>Javascipt Calculator</h1>
              <p>by Vu Long</p></code></div>

        <Container maxWidth="sm" style={{background:"rgb(255, 203, 179)", borderRadius:"5px", marginTop:"40px" }}>
            



      <Grid container spacing={6} style={{padding:"10px"}}>


        <Grid style={{padding:"1px"}} item xs={12}>
          <Screen id="display" currentNumber={this.state.currentNumber} />  
        </Grid>
        
        <Grid style={{padding:"1px"}} item xs={9}>
             <Paper><Buttonn id="clear" color="secondary" name="AC" handleClick={this.handleClick} /></Paper>
        </Grid>
        <Grid style={{padding:"1px"}} item xs={3}>
              <Paper><Buttonn id="divide" style={{background:"rgb(153, 153, 153) "}} name="/" handleClick={this.handleClick} />
</Paper>
        </Grid>
        
        <Grid style={{padding:"1px"}} item xs={3}>
            <Buttonn id="seven" name="7" handleClick={this.handleClick} />
        </Grid>
        <Grid style={{padding:"1px"}} item xs={3}>
            <Buttonn id="eight" name="8" handleClick={this.handleClick} />
        </Grid>
        <Grid style={{padding:"1px"}} item xs={3}>
            <Buttonn id="nine" name="9" handleClick={this.handleClick} />
        </Grid>
        <Grid style={{padding:"1px"}} item xs={3}>
            <Buttonn id="multiply" style={{background:"rgb(153, 153, 153) "}} name="*" handleClick={this.handleClick} />
        </Grid>

        <Grid style={{padding:"1px"}} item xs={3}>
            <Buttonn id="four" name="4" handleClick={this.handleClick} />
        </Grid>
        <Grid style={{padding:"1px"}} item xs={3}>
            <Buttonn id="five" name="5" handleClick={this.handleClick} />
        </Grid>
        <Grid style={{padding:"1px"}} item xs={3}>
            <Buttonn id="six" name="6" handleClick={this.handleClick} />
        </Grid>
        <Grid style={{padding:"1px"}} item xs={3}>
            <Buttonn id="subtract" style={{background:"rgb(153, 153, 153) "}} name="-" handleClick={this.handleClick} />
        </Grid>
        
        <Grid style={{padding:"1px"}} item xs={3}>
            <Buttonn id="one" name="1" handleClick={this.handleClick} />
        </Grid>
        <Grid style={{padding:"1px"}} item xs={3}>
            <Buttonn id="two" name="2" handleClick={this.handleClick} />
        </Grid>
        <Grid style={{padding:"1px"}} item xs={3}>
            <Buttonn id="three" name="3" handleClick={this.handleClick} />
        </Grid>
        <Grid style={{padding:"1px"}} item xs={3}>
            <Buttonn id="add" name="+" style={{background:"rgb(153, 153, 153) "}} handleClick={this.handleClick} />
        </Grid>
        
        <Grid style={{padding:"1px"}} item xs={3}>
            <Buttonn id="zero" name="0" handleClick={this.handleClick} />
        </Grid>
        <Grid style={{padding:"1px"}} item xs={3}>
            <Buttonn id="decimal" name="."  handleClick={this.handleClick} />
        </Grid>
        <Grid style={{padding:"1px"}} item xs={6}>
            <Buttonn id="equals" color="primary" name="=" handleClick={this.handleClick} />
        </Grid>




        
      </Grid>





        </Container>
      </div>
    );
  }
}

class Screen extends React.Component {
  render() {
    return (
      <div id={this.props.id}>
          <h1>{this.props.currentNumber}</h1>
        

      </div>
    )
  }
}

class Buttonn extends React.Component {
  runParentHandleClick = () => {
    this.props.handleClick(this.props.name)
  }
  render() {
    return (
      // <button id={this.props.id} onClick={this.runParentHandleClick} className="button">{this.props.name}</button>
      <Button fullWidth style={this.props.style} variant="contained" color={this.props.color} id={this.props.id} onClick={this.runParentHandleClick} className="button" ><h2>{this.props.name}</h2></Button>
    )
  }
}




function App() {
  return (
    <div className="App">
      <Application />
    </div>
  );
}

export default App;
